/* Conteneur principal */
.container {
  display: flex;
  flex-direction: column; /* Affiche les enfants en colonne */
  align-items: center;    /* Centre horizontalement */
  margin: 20px;
}

/* Wrapper des boxes */
.box-wrapper {
  display: flex;
  gap: 20px;          /* Espace entre les boîtes */
  justify-content: center; /* Centre les boîtes horizontalement */
  flex-wrap: wrap;    /* Permet le passage à la ligne si nécessaire */
}

/* Boîte individuelle */
.box {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  padding: 20px;
  text-align: center;
  width: 250px;
}

/* Titre de chaque boîte */
.title {
  background-color: #4a90e2;
  color: white;
  padding: 10px 0;
  font-size: 1.2rem;
  margin: -20px -20px 10px -20px;
}

/* Icônes */
.icon {
  font-size: 2.5rem;
  margin: 10px 0;
  color: #4a90e2;
}

/* Valeur principale */
.main-value {
  font-size: 2rem;
  font-weight: bold;
  margin: 10px 0;
}

/* Sous-valeurs */
.sub-values {
  font-size: 0.9rem;
  color: #777;
  margin-bottom: 10px;
}

/* Temps écoulé */
.elapsed-time {
  font-size: 0.8rem;
  color: #888;
}

/* Style pour le titre principal */
.main-title {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
  color: #333;
  margin-bottom: 20px; /* Espace entre le titre et les boîtes */
}
